  width: fit-content;
}
.feedback__quote-header dl,
.feedback__quote-header dd {
  margin: 0;
}
.feedback__quote-header dl {
  grid-column: 3/4;
  margin-left: auto;
  display: flex;
  column-gap: 2vw;
  row-gap: 10px;
  flex-wrap: wrap;
}
@media (min-width: 1441px) {
  .feedback__quote-header dl {
    column-gap: 28px;
  }
}
@media (max-width: 1199px) {
  .feedback__quote-header dl {
    column-gap: 26px;
  }
}
@media (max-width: 767px) {
  .feedback__quote-header dl {
    grid-column: 1/-1;
    margin: 0;
  }
}
.feedback__quote-header dt {
  font-size: 12px;
  line-height: 12px;
  letter-spacing: 0.12px;
  text-transform: uppercase;
  color: #316dc2;
}
.feedback__quote-header dd {
  font-size: 14px;
  line-height: 18px;
}
@media (max-width: 767px) {
  .feedback__quote-header dd {
    font-size: 12px;
    line-height: 14px;
    opacity: 0.9;
  }
}

.feedback__table-info {
  display: flex;
  flex-direction: column;
  row-gap: 5px;
}
@media (max-width: 767px) {
  .feedback__table-info {
    row-gap: 7px;
  }
}

.feedback__quote h3 {
  margin: 0;
  margin-bottom: 14px;
  font-size: 24px;
  font-weight: 400;
  line-height: 26px;
  color: #316dc2;
}
@media (max-width: 1199px) {
  .feedback__quote h3 {
    margin-bottom: 17px;
    font-size: 20px;
    line-height: 24px;
  }
}
@media (max-width: 767px) {
  .feedback__quote h3 {
    margin-bottom: 15px;
    font-size: 16px;
    line-height: 18px;
  }
}
.feedback__quote p {
  margin: 0;
}
@media (max-width: 1199px) {
  .feedback__quote p {
    line-height: 20px;
  }
}
@media (max-width: 767px) {
  .feedback__quote p {
    font-size: 14px;
    line-height: 20px;
  }
}

.feedback__scrollbar {
  position: relative;
  height: 2px;
  grid-column: 1/3;
  width: 78.7vw;
  max-width: 1133px;
  background-color: rgba(49, 109, 194, 0.2);
  border-radius: 0;
}
.feedback__scrollbar div {
  width: 392px;
  height: 2px;
  background-color: #316dc2;
  border-radius: 0;
  cursor: pointer;
}
@media (max-width: 1199px) {
  .feedback__scrollbar {
    margin-left: 45px;
  }
}
@media (max-width: 767px) {
  .feedback__scrollbar {
    display: none;
  }
}

.feedback__swiper-btns {
  display: flex;
  grid-column: 3/-1;
  justify-self: end;
  gap: 11px;
}
@media (max-width: 1199px) {
  .feedback__swiper-btns {
    margin-right: 45px;
  }
}
@media (max-width: 767px) {
  .feedback__swiper-btns {
    grid-row: 1/2;
    grid-column: 2/3;
    margin-right: 0;
    gap: 10px;
  }
}

.contacts {
  padding-top: 117px;
  padding-bottom: 120px;
  color: #ffffff;
  background-color: #316dc2;
}
@media (max-width: 1199px) {
  .contacts {
    padding-top: 100px;
    padding-bottom: 101px;
  }
}
@media (max-width: 767px) {
  .contacts {
    padding-top: 71px;
    padding-bottom: 67px;
  }
}

.contacts__info {
  position: relative;
  display: grid;
  grid-template-columns: min-content 48.6%;
  justify-content: space-between;
  padding-bottom: 33px;
}
.contacts__info::after {
  position: absolute;
  content: "";
  bottom: 0;
  width: 100%;
  height: 2px;
  background-color: rgba(255, 255, 255, 0.2);
}
@media (max-width: 1199px) {
  .contacts__info {
    display: flex;
    flex-direction: column;
    justify-content: unset;
    row-gap: 28px;
    padding-bottom: 26px;
  }
}
@media (max-width: 767px) {
  .contacts__info {
    row-gap: 29px;
  }
}
.contacts__info h2 {
  margin: 0;
  font-size: 50px;
  font-weight: 400;
  line-height: 50px;
  color: #316dc2;
  color: #ffffff;
  line-height: 59px;
}
@media (max-width: 1199px) {
  .contacts__info h2 {
    font-size: 34px;
    line-height: 50px;
  }
}
@media (max-width: 767px) {
  .contacts__info h2 {
    font-size: 20px;
    line-height: 20px;
  }
}
.contacts__info ul {
  margin: 0;
  padding: 0;
  display: flex;
  grid-column: 2/3;
  gap: 50px;
  list-style: none;
}
@media (max-width: 1199px) {
  .contacts__info ul {
    display: grid;
    grid-template-columns: minmax(auto, 40%) auto auto;
    justify-content: space-between;
  }
}
@media (max-width: 767px) {
  .contacts__info ul {
    display: flex;
    flex-direction: column;
    gap: 13px;
  }
}

.contacts__item {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.contacts__item h3 {
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 12px;
  letter-spacing: 0.12px;
  text-transform: uppercase;
  opacity: 0.8;
}
.contacts__item address,
.contacts__item a {
  font-size: 16px;
  font-style: normal;
  line-height: 26px;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .contacts__item address,
.contacts__item a {
    line-height: 21px;
  }
}
@media (max-width: 767px) {
  .contacts__item address,
.contacts__item a {
    font-size: 14px;
    line-height: 20px;
  }
}
@media (max-width: 1199px) {
  .contacts__item {
    gap: 18px;
  }
}
@media (max-width: 767px) {
  .contacts__item {
    gap: 9px;
  }
}
.contacts__item a {
  transition: all 0.3s ease-in;
}
@media (hover: hover) {
  .contacts__item a:hover:not(.focus-visible) {
    text-decoration: underline;
    text-underline-offset: 5px;
  }
  .contacts__item a:hover:not(.focus-visible):active {
    text-decoration: none;
    opacity: 0.8;
  }
}
.contacts__item a:active {
  opacity: 0.8;
}
.contacts__item a:focus, .contacts__item a:focus-visible {
  text-decoration: underline;
  text-underline-offset: 5px;
  outline: none;
}

.contacts__map {
  height: 500px;
  width: 100%;
  margin-top: 40px;
  border-radius: 16px;
}
@media (max-width: 1199px) {
  .contacts__map {
    margin-top: 30px;
  }
}
@media (max-width: 767px) {
  .contacts__map {
    height: 240px;
  }
}

.footer {
  padding-top: 60px;
  padding-bottom: 52px;
}
@media (max-width: 767px) {
  .footer {
    padding-top: 28px;
    padding-bottom: 40px;
  }
}

.footer__wrapper {
  display: grid;
  grid-template-columns: 52px 75% auto;
  grid-template-rows: auto auto;
  column-gap: 52px;
  row-gap: 26px;
  align-items: center;
}
@media (max-width: 1199px) {
  .footer__wrapper {
    grid-template-columns: 52px auto auto;
    column-gap: 98px;
    row-gap: 25px;
    justify-content: space-between;
  }
}
@media (max-width: 767px) {
  .footer__wrapper {
    grid-template-columns: auto;
    justify-content: center;
    row-gap: 15px;
  }
}

.footer__logo-link {
  width: fit-content;
  transition: opacity 0.3s ease-in;
}
@media (max-width: 767px) {
  .footer__logo-link {
    margin: 0 auto;
  }
}
@media (hover: hover) {
  .footer__logo-link:hover:not(.focus-visible) {
    opacity: 0.7;
  }
  .footer__logo-link:hover:not(.focus-visible):active {
    opacity: 0.7;
  }
}
.footer__logo-link:active, .footer__logo-link:focus, .footer__logo-link:focus-visible {
  opacity: 0.7;
  outline: none;
}

.footer__nav ul,
.footer__socials ul {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
}

.footer__nav ul {
  gap: 30px;
}
@media (max-width: 1199px) {
  .footer__nav {
    grid-column: 2/-1;
  }
  .footer__nav ul {
    gap: 15px;
  }
}
@media (max-width: 767px) {
  .footer__nav {
    grid-column: unset;
  }
  .footer__nav ul {
    justify-content: center;
    column-gap: 30px;
    row-gap: 10px;
    font-size: 14px;
    line-height: 20px;
  }
}

.footer__nav-link {
  transition: color 0.3s ease-in;
}
@media (hover: hover) {
  .footer__nav-link:hover:not(.focus-visible) {
    color: #316dc2;
  }
  .footer__nav-link:hover:not(.focus-visible):active {
    color: #275699;
  }
}
.footer__nav-link:active {
  color: #275699;
}
.footer__nav-link:focus, .footer__nav-link:focus-visible {
  color: #316dc2;
  outline: none;
}

.footer__socials {
  justify-self: end;
}
.footer__socials ul {
  gap: 20px;
}
@media (max-width: 1199px) {
  .footer__socials ul {
    gap: 10px;
  }
}
@media (max-width: 1199px) {
  .footer__socials {
    justify-self: start;
    grid-row: 2/3;
    grid-column: 3/-1;
  }
}
@media (max-width: 767px) {
  .footer__socials {
    grid-column: unset;
    grid-row: unset;
    order: 10;
    margin: 0 auto;
  }
}

.footer__social-link {
  width: 36px;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: #316dc2;
  transition: background-color 0.3s ease-in;
}
.footer__social-link svg {
  fill: #ffffff;
  opacity: 1;
  transition: fill 0.3s ease-in, opacity 0.3s ease-in;
}
@media (max-width: 767px) {
  .footer__social-link svg {
    width: 12px;
    height: 12px;
  }
}
@media (max-width: 767px) {
  .footer__social-link {
    width: 26px;
    height: 26px;
  }
}
@media (hover: hover) {
  .footer__social-link:hover:not(.focus-visible) svg {
    opacity: 0.7;
  }
  .footer__social-link:hover:not(.focus-visible):active {
    background-color: #275699;
  }
  .footer__social-link:hover:not(.focus-visible):active svg {
    opacity: 1;
  }
}
.footer__social-link:active {
  background-color: #275699;
}
.footer__social-link:active svg {
  opacity: 1;
}
.footer__social-link:focus, .footer__social-link:focus-visible {
  outline: none;
}
.footer__social-link:focus svg, .footer__social-link:focus-visible svg {
  opacity: 0.7;
}

.footer__copyright {
  grid-column: 1/-1;
  display: flex;
  justify-content: space-between;
  gap: 56px;
}
@media (max-width: 1199px) {
  .footer__copyright {
    grid-column: 1/3;
    flex-direction: column-reverse;
    gap: 10px;
  }
}
@media (max-width: 767px) {
  .footer__copyright {
    grid-column: unset;
    text-align: center;
    gap: 16px;
  }
}
.footer__copyright p,
.footer__copyright a {
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}
@media (max-width: 1199px) {
  .footer__copyright p,
.footer__copyright a {
    font-size: 12px;
    line-height: 12px;
  }
}
.footer__copyright p {
  margin: 0;
}
.footer__copyright a {
  text-decoration: underline;
  text-underline-offset: 4px;
  transition: color 0.3s ease-in;
}
@media (hover: hover) {
  .footer__copyright a:hover:not(.focus-visible) {
    color: #316dc2;
  }
  .footer__copyright a:hover:not(.focus-visible):active {
    color: #275699;
  }
}
.footer__copyright a:active {
  color: #275699;
}
.footer__copyright a:focus, .footer__copyright a:focus-visible {
  color: #316dc2;
  outline: none;
}