.container {
  width: 100%;
  max-width: 1440px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 100px;

  @include vp-1199 {
    padding: 0 45px;

    &--slider {
      padding: 0;
    }
  }

  @include vp-767 {
    padding: 0 15px;
  }

  &--form {
    position: relative;
  }

  &--news {
    @include vp-767 {
      padding: 0;
    }
  }
}
