.contacts {
  padding-top: 117px;
  padding-bottom: 120px;

  color: $color-default-white;

  background-color: $color-primary;

  @include vp-1199 {
    padding-top: 100px;
    padding-bottom: 101px;
  }

  @include vp-767 {
    padding-top: 71px;
    padding-bottom: 67px;
  }
}

.contacts__info {
  position: relative;

  display: grid;
  grid-template-columns: min-content 48.6%;
  justify-content: space-between;
  padding-bottom: 33px;

  &::after {
    position: absolute;
    content: "";
    bottom: 0;

    width: 100%;
    height: 2px;

    background-color: rgba(255, 255, 255, 0.2);
  }

  @include vp-1199 {
    display: flex;
    flex-direction: column;
    justify-content: unset;
    row-gap: 28px;
    padding-bottom: 26px;
  }

  @include vp-767 {
    row-gap: 29px;
  }

  h2 {
    @include title-light;

    line-height: 59px;
  }

  ul {
    margin: 0;
    padding: 0;
    display: flex;
    grid-column: 2/3;
    gap: 50px;

    list-style: none;

    @include vp-1199 {
      display: grid;
      grid-template-columns: minmax(auto, 40%) auto auto;
      justify-content: space-between;
    }

    @include vp-767 {
      display: flex;
      flex-direction: column;
      gap: 13px;
    }
  }
}


.contacts__item {
  display: flex;
  flex-direction: column;
  gap: 16px;

  h3 {
    margin: 0;

    font-size: 12px;
    font-weight: 400;
    line-height: 12px;
    letter-spacing: 0.12px;
    text-transform: uppercase;
    opacity: 0.8;
  }

  address,
  a {
    font-size: 16px;
    font-style: normal;
    line-height: 26px;
    color: $color-default-white;

    @include vp-1199 {
      line-height: 21px;
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 20px;
    }
  }

  @include vp-1199 {
    gap: 18px;
  }

  @include vp-767 {
    gap: 9px;
  }

  a {
    transition: all $trans-default;

    @include hover {
      text-decoration: underline;
      text-underline-offset: 5px;

      &:active {
        text-decoration: none;

        opacity: 0.8;
      }
    }

    &:active {
      opacity: 0.8;
    }

    &:focus,
    &:focus-visible {
      text-decoration: underline;
      text-underline-offset: 5px;

      outline: none;
    }
  }
}

.contacts__map {
  height: 500px;
  width: 100%;
  margin-top: 40px;

  border-radius: 16px;

  @include vp-1199 {
    margin-top: 30px;
  }

  @include vp-767 {
    height: 240px;
  }
}
